body {
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
}

.container {
  height: 60px;
  line-height: 60px;
}

.website_logo {
  width: 40px;
  height: 40px;
}

.nav__container {
  margin: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--main-color);
}

.nav__link.is-active:after {
  background-color: var(--main-color);
  content: "";
  border-radius: 4px;
  width: auto;
  height: 4px;
  display: flex;
}
